body {
  background: #f7f9fc; }

#app {
  overflow-y: scroll; }

.text-over-hide {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.pointer {
  cursor: pointer; }

.king-horizontal-nav11 li select.SlectBox option {
  background: white;
  line-height: 50px; }

.module-td .king-btn {
  cursor: default; }
  .module-td .king-btn:hover {
    opacity: 1;
    background: white; }
  .module-td .king-btn.king-btn-icon {
    width: 24px;
    cursor: pointer; }

.log-content em, .log-ip em, .module-td em {
  color: #ff7979;
  font-style: normal; }

.form-inline .form-group {
  display: inline-block;
  margin-bottom: 0;
  vertical-align: middle; }

.form-inline .checkbox, .form-inline .radio {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 0;
  vertical-align: middle; }

.form-inline .form-control {
  display: inline-block;
  width: auto;
  vertical-align: middle; }

.main .panel-default {
  border-radius: 2px; }

.main .toggle-all {
  border-radius: 2px; }

.main .form-group .king-btn {
  border-radius: 2px; }

.main .show-cols {
  max-height: 210px;
  overflow-y: auto; }

[v-cloak] {
  display: none; }

.vt {
  vertical-align: top; }

.icon-color {
  color: #b2beca; }



