@font-face {
  font-family: 'logFont';
  src:  url('fonts/logFont.eot?u3zdv2');
  src:  url('fonts/logFont.eot?u3zdv2#iefix') format('embedded-opentype'),
    url('fonts/logFont.ttf?u3zdv2') format('truetype'),
    url('fonts/logFont.woff?u3zdv2') format('woff'),
    url('fonts/logFont.svg?u3zdv2#logFont') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="log-icon-"], [class*=" log-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'logFont' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.log-icon-time:before {
  content: "\e900";
}
.log-icon-fold:before {
  content: "\e901";
}
.log-icon-open:before {
  content: "\e902";
}




